import { cssProps } from '@oracle/oraclejet-cssprops';
import { JSX, Ref } from 'preact';
import { useFormFieldContext } from '../hooks/UNSAFE_useFormFieldContext';
import { useTextFieldInputHandlers } from '../hooks/UNSAFE_useTextFieldInputHandlers';
import { classNames } from '../utils/UNSAFE_classNames';
import type { TextProps } from '../utils/UNSAFE_interpolations/text';
import { textInterpolations } from '../utils/UNSAFE_interpolations/text';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';
import { ValueUpdateDetail } from '../utils/UNSAFE_valueUpdateDetail';
import { getLoadingAriaLabel } from './FormControlUtils';
import { isInputPlaceholderShown } from './TextFieldUtils';

const rootStyles = {
  base: cssProps`${{
    // normally removing an outline can cause accessibility issues, however
    // on an input the cursor blinks so an outline is not required for
    // accessibility. As of 6-11-14 only chrome is putting
    // an outline on inputs anyway
    outlineStyle: 'none',
    outlineWidth: '0',
    boxSizing: 'border-box',
    fontFamily: 'inherit',
    lineHeight: 'inherit',
    fontWeight: 'inherit',
    fontSize: '100%',
    border: '0',
    backgroundColor: 'transparent',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-color)',
    width: '100%',
    paddingTop: '0',
    paddingRight: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-to-edge-padding)',
    paddingBottom: '0',
    paddingLeft: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-to-edge-padding)',

    '&::selection': {
      backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-selected)'
    }
  }}`,
  textarea: cssProps`${{
    paddingTop:
      'calc((var(--oj-c-PRIVATE-DO-NOT-USE-text-field-height) - 1rem * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height))/ 2 - 1px)',
    paddingBottom:
      'calc((var(--oj-c-PRIVATE-DO-NOT-USE-text-field-height) - 1rem * var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height))/ 2 - 1px)',
    resize: 'none'
  }}`,
  password: cssProps`${{
    '&::-ms-reveal': {
      display: 'none'
    }
  }}`,
  labelInsideBase: cssProps`${{
    minHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-inside-edge-height)',
    paddingTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-inside-edge-padding-top)',

    '&:hover': {
      backgroundColor: 'transparent'
    }
  }}`,
  textareaLabelInsideBase: cssProps`${{
    paddingTop: 0,
    paddingBottom: 0,
    // We are using a transparent border here instead of padding, due to how textarea scrolls the padding off the screen
    // and the text content will clash with the inside label.
    borderColor: 'transparent',
    borderStyle: 'solid',
    borderBlockStartWidth:
      'calc(var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-font-size) * var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-line-height) + var(--oj-c-PRIVATE-DO-NOT-USE-label-inside-edge-position-top))',
    // The 0.25rem is an aesthetically pleasing amount of bottom padding
    borderBlockEndWidth: '0.25rem'
  }}`,

  labelInsideNoValueAndHasFocus: cssProps`${{
    animationName:
      'var(--oj-c-PRIVATE-DO-NOT-USE-private-preact-text-field-animation-name-placeholder)',
    animationDuration: 'var(--oj-c-PRIVATE-DO-NOT-USE-animation-duration-xlong)',
    animationTimingFunction: 'var(--oj-c-PRIVATE-DO-NOT-USE-animation-ease-in)'
  }}`,

  hasStartContent: cssProps`${{
    paddingInlineStart: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-icon-to-text-padding)'
  }}`,

  hasEndContent: cssProps`${{
    paddingInlineEnd: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-icon-to-text-padding)'
  }}`,

  disabled: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-text-field-text-color-disabled)'
  }}`
};

const interpolations = [...Object.values(textInterpolations)];

const styleInterpolations = mergeInterpolations<Props>(interpolations);

type Autocomplete = 'off' | 'on' | string;

type OrigPickedPropsFromInput = Pick<
  JSX.HTMLAttributes<HTMLInputElement>,
  'autofocus' | 'id' | 'placeholder' | 'required' | 'role' | 'spellcheck' | 'type'
>;
type RenamedPropsFromInput = Omit<OrigPickedPropsFromInput, 'autofocus' | 'required'> & {
  autoFocus?: OrigPickedPropsFromInput['autofocus'];
  isRequired?: OrigPickedPropsFromInput['required'];
};
type InputProps = {
  as?: 'input';
  type?: Pick<JSX.HTMLAttributes<HTMLInputElement>, 'type'>['type'];
  rows?: never;
  inputRef?: Ref<HTMLInputElement>;
};

type TextAreaProps = {
  as: 'textarea';
  type?: never;
  rows?: number;
  inputRef?: Ref<HTMLTextAreaElement>;
};

type UniqueProps = InputProps | TextAreaProps;

type AriaProps = {
  ariaAutocomplete?: 'none' | 'inline' | 'list' | 'both';
  ariaControls?: string;
  ariaDescribedby?: string;
  ariaExpanded?: boolean;
  ariaInvalid?: string;
  ariaLabel?: string;
  ariaLabelledby?: string;
};

type Props = UniqueProps &
  TextProps &
  RenamedPropsFromInput &
  AriaProps & {
    autoComplete?: Autocomplete;
    // TODO: Should we expose has* props like these or should we pass this info
    // via FormFieldContext?  (from review on 3/8/22)
    currentCommitValue?: string;
    hasEndContent?: boolean;
    hasInsideLabel?: boolean;
    hasStartContent?: boolean;
    value?: string;
    onInput?: (detail: ValueUpdateDetail<string>) => void;
    onCommit?: (detail: ValueUpdateDetail<string>) => void;
    onKeyDown?: (event: KeyboardEvent) => void;
    onKeyUp?: (event: KeyboardEvent) => void;
  };

export const TextFieldInput = ({
  as,
  ariaAutocomplete,
  ariaControls,
  ariaDescribedby,
  ariaExpanded,
  ariaInvalid,
  ariaLabel,
  ariaLabelledby,
  autoComplete,
  autoFocus,
  currentCommitValue,
  hasEndContent = false,
  hasInsideLabel = false,
  hasStartContent = false,
  id,
  inputRef,
  placeholder,
  isRequired,
  role,
  rows,
  spellcheck,
  type,
  value = '',
  onInput,
  onCommit,
  onKeyDown,
  onKeyUp,
  ...props
}: Props) => {
  const hasValue = value !== '';
  const { isDisabled, isFocused, isLoading, isReadonly } = useFormFieldContext();
  const myPlaceholder = isInputPlaceholderShown(hasInsideLabel, hasValue, isFocused)
    ? placeholder
    : undefined;
  const { class: styleInterpolationClasses } = styleInterpolations(props);
  const inputClasses = classNames([
    'oj-c-text-field-input',
    rootStyles.base,
    as === 'textarea' && rootStyles.textarea,
    type === 'password' && rootStyles.password,
    hasInsideLabel &&
      classNames([
        rootStyles.labelInsideBase,
        as === 'textarea' && rootStyles.textareaLabelInsideBase,
        !hasValue && isFocused && rootStyles.labelInsideNoValueAndHasFocus
      ]),
    hasStartContent && rootStyles.hasStartContent,
    hasEndContent && rootStyles.hasEndContent,
    isDisabled && rootStyles.disabled,
    styleInterpolationClasses
  ]);
  const Comp = as || 'input';
  const handlers = useTextFieldInputHandlers({
    currentCommitValue,
    value,
    onInput,
    onCommit,
    onKeyDown
  });
  // the implicit default for tabindex on an input is 0, so do not explicitly set it.
  // TODO: for autoComplete='off', need to configure attrs appropriately to make sure it
  // works across browsers and versions  (from review on 3/11/22)
  return (
    <Comp
      aria-autocomplete={ariaAutocomplete}
      aria-controls={ariaControls}
      aria-describedby={ariaDescribedby}
      aria-expanded={ariaExpanded}
      aria-invalid={ariaInvalid}
      aria-label={isLoading ? getLoadingAriaLabel() : ariaLabel ? ariaLabel : undefined}
      aria-labelledby={ariaLabelledby}
      aria-required={isRequired ? true : undefined}
      autocomplete={autoComplete}
      autofocus={autoFocus}
      class={inputClasses}
      disabled={isDisabled}
      id={id}
      onKeyUp={onKeyUp}
      placeholder={myPlaceholder}
      readonly={isReadonly}
      // @ts-ignore
      ref={inputRef}
      role={role}
      rows={rows}
      spellcheck={spellcheck}
      type={type}
      value={value}
      {...handlers}
    />
  );
};
